import React, { useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { Navbar, Nav } from 'react-bootstrap';

import NavBar from './components/views/NavBar/NavBar';
import MainPage from './components/views/MainPage/MainPage';
import AboutPage from './components/views/AboutPage/AboutPage';
import ProjectPage from './components/views/ProjectPage/ProjectPage';
import SkillPage from './components/views/SkillPage/SkillPage';
import ContactPage from './components/views/ContactPage/ContactPage';

function returnPage(page) {
  if(page == 'home') {
    return MainPage;
  }
  else if (page=='about') {
    return AboutPage;
  }
  else if(page=='project') {
    return ProjectPage;
  }
  else if(page=='skills') {
    return SkillPage;
  } else if(page=='contact') {
    return ContactPage;
  }
}

function App() {
  const [page, setPage] = useState('home')

  useEffect(() => {
    document.title=`${page}`;
    return() => {
      
    }
  })

  return (
    <Router>
      <Navbar bg="light" expand="lg">
        <Navbar.Brand onClick={() => setPage('home')}>Comstering</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link onClick={() => setPage('home')}>Home</Nav.Link>
            <Nav.Link onClick={() => setPage('about')}>About Me</Nav.Link>
            <Nav.Link onClick={() => setPage('project')}>Project</Nav.Link>
            <Nav.Link onClick={() => setPage('skills')}>Skills</Nav.Link>
            <Nav.Link onClick={() => setPage('contact')}>Contact</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      
      <Route component={returnPage(page)} />
    </Router>
  );
}

export default App;